$bluebg:#0e34a0;
$greenbg:#018E42;

* {
    box-sizing: border-box;
    padding: none;
    margin: none;
    font-family: 'sf pro display', sans-serif;
    font-size: 18px;
}

._logo {
    position: absolute !important;
    top: 0;
    width: 50%;
}

#logo {
    max-width: 20%;
    position: relative;
    top: -50%;
    left: 50%;

}

.nav_bar{
    height:80px;
    border-radius:40px;
    width:74%;
    position:fixed !important;
    top:0;
	left:14%;
    z-index: 50;
    border:2px solid #fff;
    background-color:$bluebg;
    color:#fff;
    line-height: 80px;
}

.main_hero{
    height:580px;
    background-image:url(images/svg/imagination.svg);
	background-size: cover;
    background-repeat:no-repeat;
}

.headercase{
    width:35%;
    position:relative;
    top:45px; right:6%;
}

.btncase{
    width:auto;
    position:inherit !important;
    right: 8%;
}

._intro {
    position: absolute !important;
    top: 37% !important;
    left: 8%;
    color: #bcb8b1;
    font-size: medium;
    font-style: italic;
    font-weight: 200;
    width: 50%;
    text-align: center;
}

#headintro {
    background-color: $bluebg;
    height: 100vh;
    position: absolute !important;
    right: 0;
}

#_intro_msg {
    position: absolute !important;
    top: 42%;
}

#_intro_msg span {
    font-size: small;
    font-weight: 400;
}

._dropdown {
    position: absolute !important;
    bottom: 44%;
    left: 22%;
    color: #f86624 !important;
    font-size: medium !important;
    font-weight: 200;
    font-size: medium;
}

._ctadropdown {
    width: 100px;
    position: absolute !important;
    bottom: 34%;
    left: 28%;

}

._ctabtn {
    background-color: $bluebg;
    font-size: medium;
    color: #fff;
}

.ctabtn_outline {
    font-size: medium;
    color: $bluebg;
    border-color: $bluebg;
}

.col3 li {
    list-style: none;
    display: inline;
    line-height: 30px;
    ;
}

.col3 a {
    text-decoration: none;
    color: #fff;
}

.col3 a:hover {
    color: $greenbg
}

.col3 a:active {
    color: $greenbg;
}

.purpose h4 {
    color: #087CA7;
    font-size: medium;
}

.intsctn {
    height: 60vh;
    border-radius: 30px;
    background-color: $bluebg
}

.hero_title1 {
    color: #BCA0BC;
}
.hero_title2{
    color:#721817
}
.title{
    color:#721817;
}

.desc{
    font-size:xx-small;
    font-weight: 200;
    color:#99AA38
}
.photobox {
    position: relative;
    background-color: #EAEAEA;
    height: 350px;
    border-radius: 50px
}
.photobox h5{
    color:#0A210F;
    font-size:smaller;
}

.photobx {
    color: #0A210F;
    font-size: smaller;
}

.footer{
    height:16vh;
    background-color: $bluebg;
    border: 2px solid;
    position: absolute;
    bottom: 0;
    width: 100%;
}
.footer li{
    display: inline;
}
.footer li a{
    text-decoration:none;
    color:#F7EBEC;
    font-size: 12px;
    font-weight: 300;
}

.footer li a:hover{
    color: $greenbg;
}
.footer li a:active{
