}
.footer p{
    font-size: 14px;
    font-weight: 300;
    color:#F7EBEC;
}

/*   DASHBOARD STYLING STARTS HERE  */
.clearfix {
    clear: both;
  }
  
  .sidenav {
    height: 100%;
    width: 300px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgba(10, 33, 15, 0.7);
  }
  
  .sidenav a {
    padding: 15px 8px 15px 32px;
    text-decoration: none;
    font-size: 20px;
    color: #ccc;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav a:hover {
    color: #ccc;
    background-color: #10349A;
  }
  
  .main {
    transition: margin-left 0.5s;
    padding: 16px;
    margin-left: 300px;
  }
  
  .head {
    padding: 20px;
  }
  
  .col-div-6 {
    width: 50%;
    float: left;
  }
  
  .profile {
    display: inline-block;
    float: right;
    width: 160px;
  }
  
  .pro_img {
    float: left;
    width: 40px;
    margin-top: 5px;
  }
  
  .profile p {
    color: #fff;
    font-weight: 500;
    margin-left: 55px;
    margin-top: 10px;
    font-size: 13.5px;
  }
  
  .profile p span {
    font-weight: 400;
    font-size: 12px;
    display: block;
    color: #95969D;
  }
  
  .nav2 {
    display: none;
  }
  
  .col-div-3 {
    width: 25%;
    float: left;
  }
  
  .box {
    width: 85%;
    height: 100px;
    background-color: #F4F1BB;
    margin-left: 10px;
    padding: 10px;
    cursor: pointer;
  }
  
  .box p {
    font-size: 35px;
    color: #fff;
    font-weight: bold;
    line-height: 30px;
    padding-left: 10px;
    margin-top: 20px;
    display: inline-block;
  }
  
  .box p span {
    font-size: 20px;
    font-weight: 400;
    color: #779CAB;
  }
  
  .box-icon {
    font-size: 40px !important;
    float: right;
    margin-top: 35px !important;
    color: #BFACC8;
    padding-right: 10px;
  }
  
  .col-div-8 {
    width: 70%;
    float: left;
  }
  
  .col-div-4 {
    width: 30%;
    float: left;
  }
  
  .content_box {
    padding: 20px;
  }
  
  .content_box p {
    margin: 0;
    font-size: 20px;
    color: #FFF;
  }
  
  .content_box p span {
    float: right;
    background-color: $bluebg;
    padding: 3px 10px;
    font-size: 15px;
  }

  
  .box-8, .box-4 {
    width: 96.5%;
    background-color: #B9E3C6;
    height: 70vh;
  }
  
  .box-8 {
    margin-left: 10px;
    width: 95%;
  }

  table {
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    text-align: left;
    padding: 15px;
    color: #0A210F;
    border-bottom: 1px solid #B9E3C6;
  }
  
  th {
    color: #99AA38 !important;
  }
  
  .circle_wrap {
    margin: 50px auto;
    width: 180px;
    height: 180px;
    background: #F7F4F3;
    border-radius: 50%;
  }
  
  .circle_wrap .circle .mask, .circle_wrap .circle .fill {
    width: 180px;
    height: 180px;
    position: absolute;
    border-radius: 50%;
  }
  
  .circle_wrap .circle .mask {
    clip: rect(0px, 180px, 180px, 90px);
  }
  
  .circle_wrap .circle .mask .fill {
    clip: rect(0px, 90px, 180px, 0px);
    background-color: $bluebg;
  }
  
  .circle_wrap .circle .mask.full, .circle_wrap.circle .fill {
    -webkit-animation: fill ease-in-out 3s;
            animation: fill ease-in-out 3s;
    transform: rotate(126deg);
  }
  
  @-webkit-keyframes fill {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(126deg);
    }
  }
  
  @keyframes fill {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(126deg);
    }
  }
  .circle_wrap .inside_circle {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background: #fff;
    line-height: 130px;
    text-align: center;
    margin-top: 10px;
    margin-left: 10px;
    margin-left: 10px;
    position: absolute;
    z-index: 100;
    font-weight: 700;
    font-size: 2em;
  }

































@media only screen and (max-width:700px) {
    body {
        height: 100vh;
    }

    * {
        box-sizing: border-box;
        margin: none;
        padding: none;
    }

    ._logo {
        position: absolute !important;
        top: 0;
        left: 30%;
    }

    #logo {
        max-width: 50%;
        position: absolute !important;
        left: 7%;

    }

    ._intro {
        position: absolute !important;
        top: 20%;
        left: 28%;
        text-align: center;
        font-size: medium;
        font-weight: 200;
        border: 2px solid;
    }


    #headintro {
        display: none;
    }

    #sm_msg {
        position: absolute !important;
        bottom: 4%;
        text-align: center;
        width: 92%;
        font-size: small;
        color: #F7EBEC;

    }

    ._dropdown {
        position: absolute !important;
        bottom: 42%;
        left: 32%;
        color: #009ddc !important;
        font-size: medium !important;
        font-weight: 200;
        font-size: medium;
    }

    ._ctadropdown {
        width: 100px;
        position: absolute !important;
        bottom: 35%;
        left: 38%;

    }

    #ctabtn {
        background-color: $bluebg !important;
        border-radius: 20px;
        position: absolute !important;
        bottom: 20%;
        left: 36%;
        font-size: medium;
    }

    #ctabtn :hover {
        background-color: #B49A67;
        transition: 0.15s;
    }
}

.intsctn {
    height: 60vh;
    border-radius: 30px;
    background-color: #F7EBEC;
}